import React from 'react';
import { BrowserRouter, Routes, Route } from 'react-router-dom';
import { AuthProvider } from './contexts/AuthContext';
import NavBar from './components/NavBar';
import Login from './pages/Login';
import Register from './pages/Register';
import PrivateRoute from './components/PrivateRoute';
import { Container, Typography } from '@mui/material';

// Optional placeholder pages:
function Home(){
  return (
    <Container sx={{ mt: 4 }}>
      <Typography variant="h4">Welcome to MediConnect</Typography>
      <Typography sx={{ mt: 2 }}>Use the navigation to login, register or view doctors.</Typography>
    </Container>
  );
}

function DummyDoctors() {
  return <Container sx={{ mt: 4 }}><Typography>Doctors list (you'll add full UI next)</Typography></Container>;
}

export default function App(){
  return (
    <BrowserRouter>
      <AuthProvider>
        <NavBar />
        <Routes>
          <Route path="/" element={<Home/>} />
          <Route path="/login" element={<Login/>} />
          <Route path="/register" element={<Register/>} />

          <Route path="/doctors" element={
            <PrivateRoute><DummyDoctors/></PrivateRoute>
          } />
          { /* add more protected routes later (book, my-appointments, etc.) */ }
        </Routes>
      </AuthProvider>
    </BrowserRouter>
  );
}